acate corresponde al 4.7% del área mundial cosechada en otras frutas tropicales. La producción mundial de esta fruta ha mostrado un crecimiento en la última década concentrada en América latina y el Caribe.")]),_c('p',[_vm._v("En Colombia el área sembrada en aguacate está concentrada en los departamentos de Antioquia especialmente en el oriente antioqueño, en el 2023 fue el principal exportador de aguacate con una participación de 41,4% , Los departamentos de Tolima, Caldas, Santander, Bolívar, Cesar, Valle del Cauca, y Quindío, representan el 86% del total del área sembrada de aguacate en el país.")])]),_c('div',{staticClass:"col-lg-4 col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-26.png")}})])])])])])])])
